import React, { Component } from 'react';
import './MyMusic.css';
/* eslint-disable */
export default class MyMusic extends Component {
  render() {
    return (
      <div className="myMusic">
          <div className="myMusic_radio">
            <input type="radio" value="" name="radioSelect" checked="checked" />
            <input type="radio" value="" name="radioSelect" />
          </div>
          <div className="myMusic_firstTitle"></div>

          <div className="myMusic_secondTitle"></div>

      </div>
    );
  }
}



